.tab-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 60px;
  background-color: #000000;
  border-top: 1px solid #222222;
  z-index: 100;
}
.tab-bar button {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  color: #9e9e9e;
  padding: 8px 0;
  width: 100%;
  transition: color 0.2s, transform 0.1s;
  cursor: pointer;
}
.tab-bar button.active {
  color: white;
}
.tab-bar button svg {
  margin-bottom: 4px;
  opacity: 0.6;
}
.tab-bar button.active svg {
  opacity: 1;
}
.tab-bar button span {
  font-size: 0.7em;
  margin-top: 2px;
}
