* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #000000;
  color: #ffffff;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.App {
  min-height: 100vh;
  background: #000000;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.user-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
  justify-content: center;
  margin-bottom: 24px;
}

.user-card {
  background: #000000;
  border-radius: 16px;
  padding: 20px 24px 16px 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 160px;
  max-width: 220px;
  transition: transform 0.2s;
}

.user-card:hover {
  transform: scale(1.02);
}

.user-avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 16px;
  border: none;
}

.user-info {
  text-align: center;
  width: 100%;
}

.user-username {
  font-weight: 600;
  font-size: 1.2em;
  margin-bottom: 4px;
  color: #ffffff;
}

.user-id {
  color: #aaaaaa;
  font-size: 0.95em;
  margin-bottom: 16px;
}

.subscription-container {
  max-width: 340px;
  margin: 20px auto;
  background: #000000;
  padding: 24px;
  border-radius: 16px;
  text-align: center;
  width: 100%;
}

.subscription-header {
  color: #ffffff;
  text-align: center;
  margin-bottom: 24px;
  font-size: 1.4em;
}

.subscription-status {
  background: #1a1a1a;
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
  text-align: center;
}

.subscription-message {
  color: #ffffff;
  font-size: 1.1em;
  margin-bottom: 10px;
}

.action-button {
  background: #3366ff;
  color: #ffffff;
  border: none;
  padding: 12px 20px;
  border-radius: 8px;
  font-weight: 600;
  font-size: 1.0em;
  cursor: pointer;
  width: 100%;
  margin-top: 16px;
}

.action-button:hover {
  background: #2755e5;
}

.secondary-button {
  background: transparent;
  color: #ffffff;
  border: 1px solid #ffffff;
  padding: 10px 20px;
  border-radius: 8px;
  font-weight: 500;
  font-size: 0.9em;
  cursor: pointer;
  width: 100%;
  margin-top: 12px;
}

.secondary-button:hover {
  background: rgba(255, 255, 255, 0.1);
}
