.header {
    width: 100%;
    padding: 20px 0;
    background-color: #000000;
    text-align: center;
}

.header-content {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    max-width: 340px;
    margin: 0 auto;
}

.header-title {
    color: #ffffff;
    font-size: 1.6em;
    font-weight: 500;
}

.header-icon {
    position: absolute;
    right: 0;
    font-size: 1.4em;
    cursor: pointer;
}
